.timer-container {
  margin: 50px;
}

.timer-form {
  margin-bottom: 20px;
}

.timer {
  width: -moz-fit-content;
  width: fit-content;
  background-color: tomato;
  border-radius: 10px;
  padding: 15px;
  display: flex;
}

.field + .field {
  margin-left: 12px;
}

.label, .value {
  font-family: serif;
  font-size: 20px;
}

.input {
  width: 354px;
}

input[type="date"] {
  color: #fff;
  background-color: #4753ff;
  border: none;
  border-radius: 7px;
  outline: none;
  padding: 15px;
  font-family: sans-serif;
  font-size: 14px;
}

